<template>
    <div id="modifFourniture">
        <div class="container-fluid col-md-11">



            <h1>Modifier une fourniture</h1>


            <form action="#" class="row justify-content-md-center top-buffer">

                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-2">
                        <label for="designation">Désignation :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input type="text" id="designation" class="form-control" value="TQG">
                    </div>
                </div>
                    

                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-2">
                        <label for="prix">Prix :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input id="prix" class="form-control form-control-quantite" value="56">
                    </div>
                </div>
                
                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer" title="Si coché, l'enseignant devra saisir une description lors de la distribution">
                    <div class="col-md-2">
                        <label for="aSaisir">Description nécessaire :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input type="checkbox" id="aSaisir" class="form">
                    </div>
                </div>

                <div class="table-responsive top-buffer">
                    <table class="table table-striped">
                        <thead class="thead-dark">
                            <tr>
                                <th scope="col">Catégorie <i class="fa fa-chevron-down"></i></th>
                                <th scope="col" width="50"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td scope="row" id="col_fourniture">Culture générale</td>
                                <td><input type="checkbox" checked class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Outillage</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Informatique</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Micromécanique</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="col-md-12 row justify-content-md-center top-buffer">
                    <div class="col-md-2">
                        <router-link class="btn btn-primary" v-bind:to="'/gerer-fourniture'">Annuler</router-link>
                    </div>
                    <div class="col-md-2">
                        <button type="submit" class="btn btn-primary">Enregistrer</button>
                    </div>
                </div>


            </form>

        </div>


    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'modifFourniture',
    data() {
        return {
            fournitures: [],
            errors: []
        }
    },
    created() { 
        // Récupération des fournitures
        axios.get(`http://localhost:3000/fourniture`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.fournitures = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
    }

}

</script>
<!-- styling for the component -->
<style scoped>
.form-control-quantite {
    min-width: 0;
    max-width: 120px;
    display: inline;
}

form {
    max-width: 900px;
    margin: auto;
}

.top-buffer { 
    margin-top: 20px;
}

.btn-primary {
    margin-top: 10px;
}

table {
    max-width: 500px;
    margin: auto;
}
</style>


